import React, { Component } from 'react'
import moment from 'moment'
import Scrollbar from 'react-smooth-scrollbar'
import { Table, Input, Button, Icon, Tooltip, Popover, Popconfirm, notification } from 'antd'
import { Cookies } from 'react-cookie'
import { in_array, str_replace, compareByAlph, compareByDate, compareByAmount, roundFixed, numberWithCommas } from '../../../../containers/Layouts/function'
import { config } from '../../../../containers/Layouts/config'
import cls from '../../style/grid_market.scss'
import styles from '../../../../utilities/_general.scss'

import { note_columns } from '../../../Layouts/config/profile_columns'

import 'moment/locale/en-gb'
import 'moment/locale/th'

const charWidth = 7.5
const standardDateFix = 85
const standardWidth = 50
const standardWidthFix = 55
const standardWidthBucketFix = 85
const standardNumberFix = 70
const standardBranchFix = 120
const tooltip_placement = 'right'

const fontIcon = { fontSize: '1em' }
const iconRedColor = { color: '#ff6158' }
const iconGreenColor = { color: '#5cab6f' }

const onHandAmtGreaterThen = 'green'
const onHandAmtLessThen = 'red'

const { TextArea } = Input

const status_content = (
    <div className={cls['popHandle']}>
        <h5 style={{ padding: '5px 0', textAlign: 'center', color: '#FFF', backgroundColor: 'cadetblue' }}><b>Operation Status</b></h5>
        <p><b>OP:</b> On hand Operation</p>
        <p><b>OR:</b> Operation Return</p>
        <h5 style={{ padding: '5px 0', textAlign: 'center', color: '#FFF', backgroundColor: 'indianred' }}><b>Credit Status</b></h5>
        <p><b>A:</b> Approved</p>
        <p><b>C:</b> Cancelled</p>
        <p><b>R:</b> Rejected</p>
        <h5 style={{ padding: '5px 0', textAlign: 'center', color: '#FFF', backgroundColor: 'darkgray' }}><b>Sales Process</b></h5>
        <p><b>ZM:</b> On hand ZM</p> 
        <p><b>FC:</b> On hand FC</p>        
        <p><b>TM:</b> On hand TM</p>       
        <p><b>CA:</b> On hand CA</p>    
    </div>
)

const amount_onhand_content = (
    <div className={cls['popHandle']}>
        <p>TM <span style={{ color: onHandAmtGreaterThen }}>วงเงินเป็นสีเขียว = {`FC >= CA`}</span></p>        
        <p>TM <span style={{ color: onHandAmtLessThen }}>วงเงินเป็นสีแดง = {`FC < CA`}</span></p>
        <div style={{ height: '7px', borderTop: '1px solid #D1D1D1' }}  />
        <p>ZM <span style={{ color: onHandAmtGreaterThen }}>วงเงินเป็นสีเขียว = {`FC >= CA`}</span></p>        
        <p>ZM <span style={{ color: onHandAmtLessThen }}>วงเงินเป็นสีแดง = {`FC < CA`}</span></p>
    </div>
)

const cookies = new Cookies()
const cookieData = cookies.get('authen_info', { path: '/' })
const auth_data = (cookieData) ? cookieData.Auth : null

export const customer_column_modal = [  
    {
        title: (<Icon type="plus-square-o" className={` ${cls['mh0']}`} />),
        dataIndex: 'Collapse',
        className: `mktcol_0 ttu tracked ${cls['tc']} ${cls['ph0']} pointer`,
        width: 35,
        render: (str_date) => {
            return (str_date) ? moment(str_date, 'YYYY-MM-DD').format('DD/MM/YY') : null
        }
    },
    {
        title: (<span>App<br/>Date</span>),
        dataIndex: 'AppInDate',
        className: `mktcol_1 ttu tracked tc ${cls['letter_spece']} pointer`,
        width: standardDateFix,
        sorter: (a, b) => compareByDate(a.AppInDate, b.AppInDate),
        onHeaderCell: () => {
            return {
                onClick: () => {
                    let element = $('th.ant-table-column-has-filters.mktcol_1').find('.ant-table-column-sorter > span')
                    headAutoSort(element)                    
                }
            }
        },
        render: (str_date) => {
            return (str_date) ? moment(str_date, 'YYYY-MM-DD').format('DD/MM/YY') : null
        }
    },
    {
        title: 'Customer Information',
        dataIndex: 'CustomerCharecter',
        className: 'ttu tracked',
        children: [
            {
                title: 'Name',
                dataIndex: 'AccountName',
                className: `mktcol_2 ${cls['colnowrap']} ${cls['twice']} ${cls['customer_name']} ttu tracked pointer`,
                width: 100,
                sorter: (a, b) => compareByAlph(a.AccountName, b.AccountName),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_2').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (strText, record) => {
                    let AliasName = (record.LoanerAliasName) ? `(${keywordMatch(record.LoanerAliasName)})` : ''
                    let ApplicationNo = (record.ApplicationNo) ? `${record.ApplicationNo}` : ''
                    let Cust_CIF = (record.CIFNO) ? `${record.CIFNO}` : ''
                    let ContactTel  = (record.ContactTel && record.ContactTel != '') ? `${record.ContactTel}` : null

                    let pattern = ["น.ส.", "น.ส. ", "นาง", "นาง ", "นาย ", "นาย"];
                    let name = str_replace(pattern, "", strText)

                    const content = (
                        <div style={{ fontSize: '1em' }}>
                          <div><b>Application No:</b> {`${ApplicationNo}`}</div>
                          <div><b>CIF No:</b> {`${Cust_CIF}`}</div>
                          <div><b>Customer:</b> {`${strText} ${AliasName}`}</div> 
                          <div><b>Contact Tel:</b> {`${(ContactTel) ? ContactTel.replace(/(\d{3})(\d{3})(\d{4})/, '$1-$2-$3') : '-'}`}</div>
                        </div>
                    )
                    
                    return (
                        <Popover content={content}>
                             <span onDoubleClick={copyToClipboard.bind(this, ApplicationNo)} title="กรณีต้องการคัดลอกหมายเลข App No กดดับเบิ้ลคลิก">{`${name}`}</span>
                        </Popover>
                    )

                }
            },
            {
                title: 'Business',
                dataIndex: 'BusinessTypeApp',
                className: `mktcol_3 ${cls['business_desc']} ttu tracked pointer`,
                sorter: (a, b) => compareByAlph(a.BusinessTypeApp, b.BusinessTypeApp),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_3').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (text) => {
                    let calStr = (text && text.length > 0) ? (charWidth * text.length) : 0
                    if (calStr >= 95) {
                        return (<Tooltip placement={tooltip_placement} title={text}>{text}</Tooltip>)
                    } else {
                        return text
                    }
                }
            }
        ]
    },
    {
        title: 'Location Info',
        dataIndex: 'BusinessCharecter',
        className: 'ttu tracked',
        widht: 90,
        children: [
            {
                title: 'Market',
                dataIndex: 'MarketCode',
                className: 'mktcol_4 ttu tracked tc pointer',
                width: standardWidth,
                sorter: (a, b) => compareByAlph(a.MarketCode, b.MarketCode),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_4').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (str, data) => {
                    var market = (str) ? str.replace('M', '') : ''
                    return (<Tooltip placement={tooltip_placement} title={data.MarketName}>{market}</Tooltip>)
                }
            },
            {
                title: 'BR',
                dataIndex: 'BranchCode',
                className: 'mktcol_5 ttu tracked tc pointer',
                width: 35,
                sorter: (a, b) => compareByAmount(a.BranchCode, b.BranchCode), 
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_5').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (str, data) => {
                    return (<Tooltip placement={tooltip_placement} title={`${(data && data.BranchName) ? data.BranchName : ''}`}>{str}</Tooltip>)
                }
            },
            {
                title: 'PD',
                dataIndex: 'CampaignCode',
                className: 'mktcol_6 ttu tracked tc pointer',
                width: 30,
                sorter: (a, b) => compareByAlph(a.Product, b.Product),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_6').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            }
        ]
    },
    {
        title: (<Popover placement="topRight" content={status_content}>Status Information <i className="fa fa-search white"></i></Popover>),
        dataIndex: 'StatusLayout',
        className: 'ttu tracked ',
        children: [
            {
                title: 'CA Name',
                dataIndex: 'CAName',
                className: `mktcol_7 ttu tracked ${cls['ca_name']} pointer`,
                width: 80,
                sorter: (a, b) => compareByAlph(a.CAName, b.CAName),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_7').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },               
                render: (strText, rowData) => {
                    let owner_port = (rowData.CA_OwnerName) ? rowData.CA_OwnerName : null
                    if(owner_port && owner_port !== '') {
                        return (
                            <div>
                                <Tooltip title={owner_port} placement="top"><div className={cls['triangle_up_right']}>&nbsp;</div></Tooltip>
                                <Tooltip placement={tooltip_placement} title={strText}>{strText}</Tooltip>
                            </div>
                        )
                    } else {
                        return (<Tooltip placement={tooltip_placement} title={strText}>{strText}</Tooltip>)
                    }
                    
                }
            },
            {
                title: 'ST',
                dataIndex: 'StatusDigit',
                className: `mktcol_8 ${cls['mktcol_8']} ttu tracked tc pointer`,
                sorter: (a, b) => compareByAlph(a.StatusDigit, b.StatusDigit),
                widht: 30,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_8').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (strText, record) => {
                    let status = strText
                    if(!_.isEmpty(record) && record.StatusDigit == 'BM') {                        
                        status = 'TM'
                    } 

                    return (<Tooltip placement={tooltip_placement} title={record.Status}><span style={{ minWidth: '24px', maxWidth: '24px' }}>{status}</span></Tooltip>)
                }
            },
            {
                title: 'Date',
                dataIndex: 'StatusDate',
                className: `mktcol_9 ttu tracked tc ${cls['letter_spece']} pointer`,
                width: standardDateFix,
                sorter: (a, b) => compareByDate(a.StatusDate, b.StatusDate),  
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_9').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                render: (str_date) => {                    
                    return (str_date && str_date !== '1900-01-01T00:00:00.000Z') ? moment(str_date).format('DD/MM/YY') : null
                }
            },
            {
                title: (<span>AMT <Popover content={amount_onhand_content}><i className="fa fa-info-circle white"></i></Popover></span>),
                dataIndex: 'Limit',
                className: 'mktcol_10 ttu tracked tr pointer',
                width: standardNumberFix,
                sorter: (a, b) => compareByAmount(a.Limit, b.Limit),               
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_10').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (str_amt, rowData) => {
                    let color = {}

                    let credit_limit = (rowData && rowData.CreditLimit > 0) ? rowData.CreditLimit : 0
                    let fieldcheck_limit = (rowData && rowData.FieldCheckerLimit > 0) ? rowData.FieldCheckerLimit : 0

                    if(!_.isEmpty(rowData) && rowData.StatusDigit == 'BM') {                        
                        if(fieldcheck_limit >= credit_limit) {
                            color = { color: onHandAmtGreaterThen }
                        }
                        if(fieldcheck_limit < credit_limit) {
                            color = { color: onHandAmtLessThen }
                        }

                        const limit = (
                            <div className={cls['popHandle']}>
                                <p><b>CA :</b> {numberWithCommas(roundFixed(credit_limit, 0))}</p>
                                <p><b>FC :</b> {numberWithCommas(roundFixed(fieldcheck_limit, 0))}</p>
                            </div>
                        )     
                                               
                        return (str_amt) ? <Popover content={limit}><span style={color}>{`${numberWithCommas(roundFixed(str_amt, 0))}`}</span></Popover> : 0

                    } else {
                        return numberWithCommas(roundFixed(str_amt, 0))
                    }

                    
                }
            }
        ]
    },
    {
        title: (<span>Setup<br/>Date</span>),
        dataIndex: 'SetupDate',
        className: `mktcol_11 ttu tracked tc ${cls['letter_spece']} pointer`,
        width: 45,
        sorter: (a, b) => compareByDate(a.SetupDate, b.SetupDate),  
        onHeaderCell: () => {
            return {
                onClick: () => {
                    let element = $('th.ant-table-column-has-filters.mktcol_11').find('.ant-table-column-sorter > span')
                    headAutoSort(element)                    
                }
            }
        }, 
        render: (str_date, rowData) => {
            let setup_date = (!_.isEmpty(rowData.SetupDate) && rowData.SetupDate !== '1900-01-01T00:00:00.000Z') ? rowData.SetupDate : null
            let start_month = 0
            let current_date = moment()
            
            if(!_.isEmpty(setup_date)) {
                let setup_moment = moment(setup_date)
                start_month = moment([current_date.format('YYYY'), current_date.format('MM'), current_date.format('DD')]).diff(moment([setup_moment.format('YYYY'), setup_moment.format('MM'), setup_moment.format('DD')]), 'months', true)
            }

            let setup_data = (str_date && str_date !== '1900-01-01T00:00:00.000Z') ? moment(str_date, 'YYYY-MM-DD').format('DD/MM/YYYY') : null
            return (
                <Tooltip placement="right" title={`${setup_data} (${(start_month && start_month > 0) ? roundFixed(start_month, 0) : 0} months ago)`}>
                    {`${(str_date && str_date !== '1900-01-01T00:00:00.000Z') ? moment(str_date, 'YYYY-MM-DD').format('DD/MM') : null}`}
                </Tooltip>
            )

        }
    },
    {
        title: 'O/S Bal.',
        dataIndex: 'LoanSetup',
        className: 'ttu tracked',
        children: [
            {
                title: 'Amt',
                dataIndex: 'Principle',
                className: 'mktcol_12 ttu tracked tr pointer',
                width: standardNumberFix,
                sorter: (a, b) => compareByAmount(a.Principle, b.Principle),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_12').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                render: (str_amt, rowData) => {
                    const content = (
                        <div style={{ fontSize: '1em' }}>
                            <div>
                                <b>O/S START: </b>  
                                {` ${(rowData && rowData.PrincipleStart) ? numberWithCommas(roundFixed(rowData.PrincipleStart, 0)) : 0}`}
                            </div>
                        </div>
                    )
                    
                    return (
                        <Popover content={content}>
                             <span>{`${(str_amt) ? numberWithCommas(roundFixed(str_amt, 0)) : 0}`}</span>
                        </Popover>
                    )
                }
            },
            {
                title: 'NU',
                dataIndex: 'IsTopup',
                className: `mktcol_13 ttu tracked tc ${cls['letter_spece']} pointer`,                
                width: 30,
                sorter: (a, b) => compareByAlph(a.IsTopup, b.IsTopup),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_13').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (flag) => {
                    return (flag && flag == 'Y') ? flag:''
                }
            }
        ]
    },
    {
        title: 'Cycle Due',
        dataIndex: 'WkCycle',
        className: 'ttu tracked',
        children: [           
            {
                title: 'Due',
                dataIndex: 'MthCycleDueDay',
                className: 'mktcol_14 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.MthCycleDueDay, b.MthCycleDueDay),
                width: standardWidth,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_14').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            },
            {
                title: 'Day',
                dataIndex: 'WkCycleDue',
                className: 'mktcol_15 ttu tracked tc pointer',
                sorter: (a, b) => compareByAlph(a.WkCycleDue, b.WkCycleDue),
                width: standardWidth,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_15').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            },
            {
                title: (<Tooltip title="Yesterday Priority">P</Tooltip>),
                dataIndex: 'Yesterday_Priority',
                className: 'mktcol_16 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.Yesterday_Priority, b.Yesterday_Priority),
                width: 25,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_16').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            },
            {
                title: 'WK',
                dataIndex: 'Week_Installment',
                className: 'mktcol_17 ttu tracked tr pointer',
                width: standardNumberFix,
                sorter: (a, b) => compareByAmount(a.Week_Installment, b.Week_Installment),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_17').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                render: (str_amt) => {
                    return (str_amt) ? numberWithCommas(str_amt) : 0
                }
            },
            {
                title: 'Mth',
                dataIndex: 'Mth_Installment',
                className: 'mktcol_18 ttu tracked tr pointer',
                width: standardNumberFix,
                sorter: (a, b) => compareByAmount(a.Mth_Installment, b.Mth_Installment),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_18').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                render: (str_amt) => {
                    return (str_amt) ? numberWithCommas(str_amt) : 0
                }
            }
        ]
    },   
    {
        title: 'DPD BUCKET',
        dataIndex: 'DPDBucketInformation',
        className: 'ttu tracked',
        children: [
            {
                title: 'Start',
                dataIndex: 'Cust_DPDBucketStart',
                className: 'mktcol_19 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.Cust_DPDBucketStartSeq, b.Cust_DPDBucketStartSeq),
                width: standardWidth,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_19').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            },
            {
                title: 'Now',
                dataIndex: 'Cust_DPDBucketNow',
                className: 'mktcol_20 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.Cust_DPDBucketNowSeq, b.Cust_DPDBucketNowSeq),
                width: standardWidth,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_20').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (bucket, rowData) => {
                    let overdue_day = (rowData.OverdueDay) ? rowData.OverdueDay : 0
                    let current_point = (rowData.Cust_DPDBucketNow && in_array(rowData.Cust_DPDBucketNow, ['XDay', 'M1-2', 'NPL'])) ? 'MDPD': 'WDPD'

                    let text_bucket = ''
                    if(current_point == 'WDPD') {
                        if(overdue_day == 0)
                            text_bucket = '0 WDPD'
                        else if(overdue_day > 0 && overdue_day <= 7)
                            text_bucket = '1-7 WDPD'
                        else if(overdue_day > 8 && overdue_day <= 14)
                            text_bucket = '8-14 WDPD'
                        else if(overdue_day >= 15 && overdue_day <= 21)
                            text_bucket = '15-21 WDPD'
                        else if(overdue_day >= 22 && overdue_day <= 30)
                            text_bucket = '22-30 WDPD'
                    } else {
                        if(overdue_day > 1 && overdue_day <= 30)
                            text_bucket = '1-30 MDPD'
                        else if(overdue_day >= 31 && overdue_day <= 60)
                            text_bucket = '31-60 MDPD'
                        else if(overdue_day >= 61 && overdue_day <= 90)
                            text_bucket = '61-90 MDPD'
                        else if(overdue_day > 90)
                            text_bucket = '90+ MDPD'
                    }
                    return (<Tooltip placement="right" title={`${text_bucket}`}>{bucket}</Tooltip>)
                }
            },
            {
                title: (<Tooltip title="Priority">P</Tooltip>),
                dataIndex: 'Priority',
                className: 'mktcol_21 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.Priority, b.Priority),
                width: 25,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_21').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }
            }
        ]
    },
    {
        title: 'Overdue Info',
        dataIndex: 'Overdue',
        className: 'ttu tracked',
        children: [     
            {
                title: (<Tooltip title="Overdue Day"><Icon type="dashboard" /></Tooltip>),
                dataIndex: 'OverdueDay',
                className: 'mktcol_22 ttu tracked tc pointer',
                sorter: (a, b) => compareByAmount(a.OverdueDay, b.OverdueDay),
                width: standardWidth,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_20').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (days, rowData) => {
                    let current_point = (rowData.Cust_DPDBucketNow && in_array(rowData.Cust_DPDBucketNow, ['XDay', 'M1-2', 'NPL'])) ? 'MDPD': 'WDPD'
                    let current_dpd = (current_point == 'WDPD') ? rowData.WDPD : rowData.MDPD
                    return (<Tooltip placement="right" title={`${current_dpd} ${current_point}`}>{days}</Tooltip>)
                }
            },       
            {
                title: 'Amt',
                dataIndex: 'OverdueAmt',
                className: 'mktcol_23 ttu tracked tr pointer',
                width: standardNumberFix,
                sorter: (a, b) =>  compareByAmount(a.OverdueAmt, b.OverdueAmt),
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_21').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                render: (str_amt) => {
                    return (str_amt) ? numberWithCommas(str_amt) : 0
                }
            },
            {
                title: (<Tooltip title="MPD = ชำระเงินเพื่อให้หลุดจาก XDay"><span>MPD</span></Tooltip>),
                dataIndex: 'Mth_AmtPastDue',
                className: 'mktcol_24 ttu tracked tr pointer',
                sorter: (a, b) => compareByAmount(a.Mth_AmtPastDue, b.Mth_AmtPastDue),   
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_24').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
            
                width: standardNumberFix,
                render: (str_amt) => {
                    return (str_amt) ? numberWithCommas(str_amt) : 0
                }
            },
            {
                title: 'Last pmt',
                dataIndex: 'LastPaymentDate',
                className: 'mktcol_25 ttu tracked tr pointer',
                sorter: (a, b) => compareByDate(a.LastPaymentDate, b.LastPaymentDate),   
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_25').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                }, 
                width: 65,
                render: (str_date, record) => {                    
                    let str_amt = (record && record.LastPaymentAmt) ? numberWithCommas(record.LastPaymentAmt) : 0
                    return (<Tooltip placement={tooltip_placement} title={`ชำระล่าสุด ${str_amt}`}>{(str_date && str_date !== '1900-01-01T00:00:00.000Z') ? moment(str_date, 'YYYY-MM-DD').format('DD/MM/YY') : null}</Tooltip>)
                }
            },
            {
                title: (<div className="tc">Note</div>),
                dataIndex: 'Cause',
                className: `mktcol_26 ${cls['cause_name']} ttu tracked tl pointer`,
                sorter: (a, b) => compareByAlph(a.Cause, b.Cause),   
                width: 80,
                onHeaderCell: () => {
                    return {
                        onClick: () => {
                            let element = $('th.ant-table-column-has-filters.mktcol_26').find('.ant-table-column-sorter > span')
                            headAutoSort(element)                    
                        }
                    }
                },
                render: (strText, record) => {
                    let Cause = (record.Cause) ? `${record.Cause}` : '-'
                    let CauseDetail = (record.CauseDetail) ? `${record.CauseDetail}` : 'ไม่ระบุ'
                    let CA_NotFound = (record.CA_NotFound) ? `${record.CA_NotFound}` : null
                    let CA_Cause = (record.CA_Cause) ? `${record.CA_Cause}` : 'ไม่ระบุ'
                    let CA_Cause_Detail = (record.CA_Cause_Detail) ? `${record.CA_Cause_Detail}` : 'ไม่ระบุ'

                    let TMNote_Cause = (record.Cause) ? `${record.Cause}` : null
                    let CANote_Cause = (record.CA_Cause) ? `${record.CA_Cause}` : null

                    let note_check = (CANote_Cause && CANote_Cause !== '') ? CANote_Cause : TMNote_Cause
                
                    const content = (
                        <div style={{ fontSize: '1em' }}>
                          <div className="ttu"><b>TM Note</b></div>
                          <div style={{ fontSize: '0.9em' }}><b>เหตุผล:</b> {`${Cause}`}</div>
                          <div style={{ fontSize: '0.9em' }}><b>หมายเหตุ:</b> {`${CauseDetail}`}</div>
                          <hr/>
                          <div className="ttu"><b>CA Note</b></div>
                          {
                              (CA_NotFound) ? 
                              (<div style={{ fontSize: '0.95em' }}><b>ไม่พบ:</b> {`${CA_NotFound}`}</div>) : 
                              (
                                <div>
                                    <div style={{ fontSize: '0.95em' }}><b>เหตุผล:</b> {`${CA_Cause}`}</div>
                                    <div style={{ fontSize: '0.95em' }}><b>หมายเหตุ:</b> {`${CA_Cause_Detail}`}</div>
                                </div>
                              )
                          }
                         
                        </div>
                    )
                    
                    return (
                        <Popover content={content} placement="left">
                            {`${(note_check && note_check !== '') ? note_check : ''}`}
                        </Popover>
                    )

                }
            },
            {
                title: (<Icon type="message" />),
                dataIndex: 'IconNote',
                className: 'mktcol_21 ttu tracked tc',
                width: 25,
                render: (str, rowsData) => {
                    return (<PopoverNote data={rowsData} />)
                }
            }
        ]
    }
]

class PopoverNote extends Component {

    state = {
        gridData: []
    }

    handleRowKey = (record) => {
        return 
    }

    render() {
        const { data } = this.props

        const text = (<div>กรุณายืนยันการบันทึกข้อมูล</div>)
        const evtSaveNote = this.handleAddMessage.bind(this, { appno: `${data.ApplicationNo}`, mktcode: data.MarketCode })

        const popf_config = {
            id: `CONF_${data.ApplicationNo}`,
            title: text,
            onConfirm: evtSaveNote,
            okText: "ยืนยัน",
            cancelText: "ยกเลิก",
            onClick: handlConfirmNote.bind(this, `CONF_${data.ApplicationNo}`),
            placement: "left"
        }

        return (
            <Popover 
                trigger="click"             
                mouseEnterDelay={3} 
                placement="left" 
                className="pointer"
                onClick={this.handleProfile.bind(this, { appno: `${data.ApplicationNo}`, mktcode: data.MarketCode })} 
                content={(
                    <div id={`EL_${data.ApplicationNo}`} style={{ width: '300px', opacity: 0 }}>
                        <small className="ttu fw5" style={{ display: 'block' }}>Note Message :</small>
                        <TextArea id={`${data.ApplicationNo}`} />
                        <div style={{ textAlign: 'right' }}>
                            <Popconfirm {...popf_config}>
                                <Button type="primary" icon="save" size="small" style={{ marginTop: '5px' }} />
                            </Popconfirm>
                        </div>
                       
                        <div className={cls['grid_container']}>
                            <small className="ttu fw5" style={{ display: 'block' }}>Note History</small>
                            <Scrollbar style={{ maxHeight: '200px', minHeight: 'auto' }}>
                                <Table 
                                    rowKey="RowID"
                                    columns={note_columns} 
                                    dataSource={this.state.gridData} 
                                    className={`${cls['grid_nano_dashboard']} ${cls['mt1']}`}
                                    pagination={false}
                                    bordered
                                />
                            </Scrollbar>
                        </div>
                        
                    </div>
                )}
            >
                <Icon id={`ICON_${data.ApplicationNo}`} type="message" />
            </Popover>
        )
    }

    handleAddMessage = (objData) => {
        let el_target = document.querySelector(`#${objData.appno}`)
        let appno = (objData.appno) ? objData.appno : null
        let params = {
            MarketCode: (objData.mktcode) ? objData.mktcode : null,
            ApplicationNo: appno,
            Subject: 'อื่นๆ',
            Reason: 'N099',
            Remark: el_target.value,
            CreateID: (auth_data && !_.isEmpty(auth_data)) ? `${auth_data.EmployeeCode}`: null,
            CreateBy: (auth_data && !_.isEmpty(auth_data)) ? `${stripname(auth_data.EmpName_TH)}`: null
        }
    
        const request_set = new Request(`${config.hostapi}/noteupdate`, {
            method: 'POST',
            header: new Headers({
                'Accept': 'application/json, application/xml, text/plain, text/html, *.*',
                'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
            }),
            body: JSON.stringify(params),
            cache: 'no-cache'
        })
    
        if(el_target.value && el_target.value.length > 5) {
            let icon_target = document.querySelector(`#ICON_${appno}`)
            if(icon_target) {
                icon_target.classList.add(styles['fg_red'])
            }
        
            fetch(request_set)
            .then(response => response.json())
            .then(data => {
                if (data.status) {
                    notification.success({
                        message: 'การบันทึกข้อมูล',
                        description: 'ดำเนินการบันทึกข้อมูลสำเร็จ'
                    })
                }
            })
    
        } else {
            notification.warning({
                message: 'แจ้งเตือนจากระบบ',
                description: 'กรุณากรอกข้อมูลให้ถูกต้อง และตัวอักษรมากกว่า 5 ตัวอักษร',
              })
        }
    
    }

    handleProfile = (data) => {

        if(data.mktcode && data.appno) {
            const request_set = new Request(`${config.hostapi}/notelists/mktc/${data.mktcode}/app/${data.appno}/note_lists`, {
                method: 'GET',
                cache: 'no-cache',
                header: new Headers({
                    'Accept': 'application/json',
                    'Content-Type': 'application/json; charset="UTF-8";'
                })
            })
        
            fetch(request_set)
            .then(response => response.json())
            .then(resp => { 
                this.setState({ gridData: (resp.status) ? resp.data : []  })    
            })
            .catch(err => { console.log(`fetch error ${err}`) })

        }

        _.delay(() => {
            let e = `EL_${data.appno}`    
            _.delay(() => { 
                $(`#${e}`).css({ opacity: 1 }) 
                $(`#${e}`).addClass('animated fadeIn')
            }, 300)
            let element = $(`#${e}`).parents()
            if(element) {
                $(element[0]).css('padding', '5px')
                $(element[0]).addClass(`${cls['unset']}`)
                $(element[1]).css('background', '#FFF ')
                $(element[2]).addClass(cls['unset'])
                $(element[2]).addClass('animated bounceIn')
    
                let el_arrow = $(element[3]).children()[0]            
                if(el_arrow) {
                    $(el_arrow).addClass(cls['unset'])
                }
            }
            
        }, 200)

    }
    

}

const stripname = (str) => {
    if (str)
        return str.replace('+', ' ')
    else
        return ''
}

const copyToClipboard = (appno) => {
    let $temp = $("<input>");
    $("body").append($temp);
    $temp.val(appno).select();
    document.execCommand("copy");
    $temp.remove();

    notification.success({ message: 'Copy text to clipboard', description: `ระบบคัดลอกข้อมูลหมายเลข ${appno}` })
}

const keywordMatch = (strText) => {
    const keyword_text = ['พี่', 'พี', 'เจ๊', 'คุณ', 'ป้า', 'ลุง']
    let master_pattern = new RegExp(keyword_text.join('|'))

    if (strText && !master_pattern.test(strText)) return `พี่${strText}`
    else return (strText && strText !== '') ? strText : ''
}

const adjuctTooltip = (strText, cellWidth) => {
    if (strText) {
        let charSize = charWidth * strText.length
        return (charSize > cellWidth) ? (<Tooltip placement={tooltip_placement} title={strText}>{strText}</Tooltip>) : strText
    } else {
        return strText
    }
}

const headAutoSort = (element) => {
    if(element) {
        let el1_class = (element[0]) ? element[0].className.split(" ") : null
        let el2_class = (element[1]) ? element[1].className.split(" ") : null

        if(!in_array('on', el1_class) && !in_array('on', el2_class)) {
            $(element[0]).click()
        } else {
            if(in_array('on', el1_class)) {
                $(element[1]).click()
            }
            if(in_array('on', el2_class)) {
                $(element[0]).click()
            }
        }
    } else {
        console.log('not found element.')
    }
    
}

const handlConfirmNote = (e) => {
    _.delay(() => {
        let el_target = $(`#${e}`)
        let elements = el_target.parents()

        el_target.addClass(cls['unset'])       
        el_target.find('.ant-popover-inner-content').addClass(cls['unset'])

        if(elements) {
            $(elements[0]).addClass(`${cls['unset']} animated fadeIn`)
            $(elements[0]).find('.ant-popover-arrow').addClass(cls['unset'])
        }
        
    }, 200)
}


